export const mutations={
    // 获取个人信息
    gaininfo(state, obj) {
      state.profile = obj
      state.profiles = obj.profile
    },
    // 个人歌单 包括个人创建的收藏的
    gainpersonagemusics(state, obj) {
      state.personagemusics = obj.mymusics
      state.mylikemusic = obj.mylike
      state.collectmusics = obj.qitamusics
    },
    //  获取我的喜欢的id数组
    gainmylikemusicids(state, obj) {
      state.mylikemusicids = obj
    },
    // 获取私人FM
    gainmyFM(state, obj) {
      state.myFM = obj
    }
}