export const actions = {
  //设置audio元素
  setAudio({ state }, audio) {
    state.audio = audio;
  },
  //设置audio元素
  setscrolltops({ state }, scrolltops) {
    state.scrolltops = scrolltops;
  },
  //设置player元素
  setplayer({ state }, player) {
    state.player = player;
  },
  //改变播放顺序状态
  changestatus({ state }, status) {

    state.patternstatus = status;
  },
  //更换音频src
  changeAudioSrc({ state }, item) {
    state.audiodetail = item

    state.audioSrc = item.url;
    state.status = 1;
    // 返回dom元素
    return state.audio;
  },
  // 改变音量
  changeplayervolume({ state }, volume) {
    state.playervolume = volume;
    return volume
  },
  // 记录音频的index
  changeAudioindex({ state }, index) {

    state.audioindex = index

    return index
  },
  //添加已播放的音乐
  addPlayedMusic({ state }, music) {
    //查找是否已经存在音乐，如果存在，则不添加，否则添加
    for (let i = 0; i < state.playedMusicList.length; i++) {
      if (state.playedMusicList[i].id == music.id) {
        //找到
        return

      }
    }
    console.log("添加音乐成功");
    state.playedMusicList.unshift(music);

  },
  // 改变正在播放的音乐所处的歌单

  changeAudiocoleect({ state }, musicscollects) {
    state.audiocoollets = musicscollects

  },
  // 改变正在观察的歌单
  changeseecoleect({ state }, cickaudiocooles) {
    state.cickaudiocooles = cickaudiocooles

  },
  // 改变点击的非我的喜欢的歌单
  changesonglist({ state }, songlist) {
    state.songlist = songlist
  },
  changesonglistitem({ state }, songlist) {
    state.songlistitem = songlist
  },
  //更改音频播放状态
  changeMusicStatus({ state }, status) {
    state.status = status;
  },
  // 记录当前播放的时间
  gettimeupdates({ state }, time) {
    state.timeupdates = time
  },
  // 将歌词转化为数组
  parseLyric({ state }, lyric) {

    if (lyric == "纯音乐。请您欣赏") {

      state.lyricArray = lyric
      return
    }
    var lyrics = lyric.split("\n");//this.lrc代表歌词文件内容的引用
    var lrcObj = {};
    for (var i = 0; i < lyrics.length; i++) {

      var lyric = decodeURIComponent(lyrics[i]);
      var timeReg = /\[\d*:\d*((\.|\:)\d*)*\]/g;
      var timeRegExpArr = lyric.match(timeReg);
      if (!timeRegExpArr) continue;
      var clause = lyric.replace(timeReg, '');

      if (clause == "") {
        continue
      }
      for (var k = 0, h = timeRegExpArr.length; k < h; k++) {
        var t = timeRegExpArr[k];
        var min = Number(String(t.match(/\[\d*/i)).slice(1)),
          sec = Number(String(t.match(/\:\d*/i)).slice(1));
        var time = min * 60 + sec;
        lrcObj[time] = clause;
      }
    }
    state.lyricArray = lrcObj

    return state.lyricArray


  },
  getbannerlist({state},banner){
    state.bannerlist = banner
  }
}