<template>
<div id="app">
    <router-view />

    <div class="player-music" ref="player" @click="goplay" v-show="audioSrc==''? false:true">
        <div>
            <div class="player-left">
                <div class="player-img">
                    <img :src="picUrl" alt />
                </div>
                <div class="player-message">
                    <div class="message-top">
                        <!-- <span class="player-vip">VIP</span> -->
                        <span class="musicename">{{audiodetail.name}}{{audiodetail==""?"":audiodetail.alia[0]}}</span>
                    </div>
                    <div class="message-bom">
                        <div class>{{lyric}}</div>
                    </div>
                </div>
            </div>
            <div class="player-right">
                <div class="iconfont" @click.stop="play">{{status == 0 ? '&#xe608;' : '&#xe664;'}}</div>
                <div class="iconfont" @click.stop="popup">&#xe663;</div>
            </div>
        </div>
    </div>
    <van-popup v-model="show" round position="bottom">
        <div class="nowplay">
            当前播放
            <span class="nownum">({{audiocoollets.length}})</span>
        </div>
        <div class="control">
            <div class="pattern">
                <div v-for="(value,index) in pattern" :key="index" v-show="value.ischeak" class="iconfont" @click="changepattern(value,index)">
                    <span v-html="value.icon"></span>
                    <span>{{value.title}}</span>
                </div>
            </div>
        </div>
        <div class="audiocoollets-list">
            <div class="listcoollets-item" v-for="(value,index) in audiocoollets" :key="index" :class="[index==audioindex? 'active':'']" @click="playmusic(value,index)">
                <div class="iconfont" v-if="index==audioindex">&#xe662;</div>
                <div class="listname">{{value.name}}</div>-
                <div class="itemname">{{value.ar[0].name}}</div>
            </div>
        </div>
    </van-popup>
    <audio ref="audio" :src="audioSrc" @timeupdate="timeupdate" @ended="ended" @canplay="canplay" @durationchange="durationchange">
        <source :src="audioSrc" type="audio/ogg" />
        <source :src="audioSrc" type="audio/mpeg" />您的浏览器不支持 audio 元素。
    </audio>
</div>
</template>

<script>
import {
    createNamespacedHelpers,
    mapState,
    mapMutations
} from "vuex";
const {
    mapState: playermusicState,
    mapActions: playermusicActions,
} = createNamespacedHelpers("playermusicstore");

export default {
    data() {
        return {
            lyric: "",
            show: false,
            pattern: [{
                    icon: "&#xe506;",
                    start: 0,
                    ischeak: false,
                    title: "单曲循环"
                },
                {
                    icon: "&#xe619;",
                    start: 1,
                    ischeak: false,
                    title: "随机"
                },
                {
                    icon: "&#xe60b;",
                    start: 2,
                    ischeak: false,
                    title: "列表循环"
                },
            ],
        };
    },
    created() {
        this.$nextTick(() => {
            console.log(this.$refs.audio);
            //获取audio元素
            this.setAudio(this.$refs.audio);
            let volume = this.$refs.audio.volume;
            console.log("volume", volume);
            this.changeplayervolume(volume);
        });
        // this.show=this.shows

        this.$nextTick(() => {
            console.log(this.$refs.player);
            //获取播放器元素
            this.setplayer(this.$refs.player);
        });
    },
    computed: {
        // 音频src 音频dom 音频状态
        ...playermusicState([
            "audioSrc",
            "audio",
            "status",
            "audiodetail",
            "lyricArray",
            "timeupdates",
            "audiocoollets",
            "patternstatus",
            "audioindex",
        ]),

        picUrl() {
            let playermusicstore = this.$store.state.playermusicstore;
            let picUrl;
            if (playermusicstore.audiodetail == "") {
                picUrl = "aaa";
            } else {
                picUrl = playermusicstore.audiodetail.al.picUrl;
            }

            return picUrl;
        },
    },
    methods: {
        ...playermusicActions([
            "setAudio",
            "changeMusicStatus",
            "setplayer",
            "gettimeupdates",
            "changeplayervolume",
            "changestatus",
            "addPlayedMusic",
            "changeAudioSrc",
            "changeAudioindex",
            "parseLyric"
        ]),
        ...mapMutations(["changeshow"]),
        play() {
            let playStatus = this.status == 0 ? "play" : "pause";
            let status = this.status == 0 ? 1 : 0;

            this.changeMusicStatus(status).then(() => {
                this.audio[playStatus]();
            });
        },
        // 	当目前的播放位置已更改时触发。
        timeupdate() {
            let time = Math.floor(this.audio.currentTime);
            let obj = this.lyricArray[time];
            this.gettimeupdates(time);
            if (obj != undefined) {
                this.lyric = obj;
            }
        },
        // 弹出正在播放的列表
        popup() {
            this.show = !this.show;
            this.pattern.map((v) => {
                if (v.start == this.patternstatus) {
                    v.ischeak = true;
                } else {
                    v.ischeak = false;
                }
            });
        },
        // 换音乐
        durationchange() {
            this.lyric = this.lyricArray[0];
        },
        // 切换播放模式
        changepattern(value, index) {
            value.ischeak = false;

            let status = index;
            status++;
            if (status >= 3) {
                status = 0;
            }
            this.pattern[status].ischeak = true;
            this.changestatus(status);
        },
        // 播放结束
        ended() {

            let audioindex = this.audioindex;
            console.log("this.patternstatus", this.patternstatus);
            if (this.patternstatus == 1) {
                audioindex = Math.floor(Math.random() * this.audiocoollets.length);
                this.changeAudioindex(audioindex).then((result) => {
                    console.log("xudio", result);
                    this.playmusic(null, result);
                });
            } else if (this.patternstatus == 2) {
                audioindex++;
                if (audioindex > this.audiocoollets.length - 1) {
                    audioindex = 0;
                }

                this.changeAudioindex(audioindex).then((result) => {
                    console.log("xudio", result);
                    this.playmusic(null, result);
                });
            } else {
                audioindex = this.audioindex;
                this.changeAudioindex(audioindex).then((result) => {
                    console.log("xudio", result);
                    this.playmusic(null, result);
                });
            }

        },
        goplay() {
            this.$router.push({
                name: "playermusic"
            });
        },
        canplay() {
            this.audio.play();
            this.changeMusicStatus(1);
        },
        playmusic(item, index) {
            console.log(item);
            let items = item
            let id
            if (item) {
                id = items.id
                if (item.url == null) {
                    this.$toast("因api不提供url原因，该资源无法播放");
                    return;
                }
            }
            if (!item) {
                console.log("进来了");
                items = this.audiocoollets[index];
                console.log("进来了", items);
                id = items.id
                console.log("进来了", id);
            }
            this.axios({
                    method: "GET",
                    url: "/lyric",
                    withCredentials: true,
                    params: {
                        id: id
                        //   id: 4341314,
                    },
                })
                .then((result) => {
                    console.log("请求歌词成功", result);
                    let lyric;
                    if (!result.data.needDesc) {
                        lyric = result.data.lrc.lyric;
                    } else {
                        lyric = "纯音乐。请您欣赏";
                    }
                    this.parseLyric(lyric);
                })
                .catch((err) => {
                    console.log(err);
                });
            this.changeAudioindex(index);
            this.changeAudioSrc(items).then((audio) => {
                audio.currentTime = 0;
                //添加播放的音乐
                this.addPlayedMusic(items);
            });
        },
    },
};
</script>

