import Vue from 'vue'
import App from './App.vue'
// 导入路由
import router from './router'
import store from './store'
// 导入字体
import "./assets/font/iconfont.css"

import 'lib-flexible/flexible'

import axios from 'axios'
import VueAxios from 'vue-axios'
import VueRouter from 'vue-router'


Vue.use(VueAxios, axios)



import {
  Button,
  Checkbox,
  CheckboxGroup,
  NavBar,
  Toast,
  Field,
  PasswordInput,
  NumberKeyboard,
  Tab,
  Tabs,
  Slider,
  Swipe,
  SwipeItem,
  Sticky ,
  Tabbar ,
  TabbarItem ,
  Popup ,


} from 'vant';
Vue
  .use(Button)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(NavBar)
  .use(Toast)
  .use(Field )
  .use(PasswordInput)
  .use(NumberKeyboard)
  .use(Tabs)
  .use(Tab)
  .use(Slider)
  .use(Swipe)
  .use(SwipeItem)
  .use(Sticky )
  .use(Tabbar )
  .use(TabbarItem )
  .use(Popup )

axios.defaults.baseURL = 'http://59.110.1.81:3000/'
// axios.defaults.baseURL = 'http://music.kele8.cn'

Vue.config.productionTip = false

let originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function (url) {
  return originalPush.call(this, url).catch(err => err);
}

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')
